.image-carousel-wrapper {
  width: 100%;
  padding: 40px 0;
  position: relative; /* For stacking context */
  z-index: 1; /* To appear above the fixed background */
}

.image-carousel-swiper {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding-bottom: 50px !important; /* Space for pagination */
}

.image-carousel-swiper .swiper-slide {
  width: 60%; /* Define a specific width for each slide */
  max-width: 500px;
  height: auto;
  transition: opacity 0.4s ease-in-out, transform 0.4s ease-in-out;
  transform: scale(0.8);
  opacity: 0.5;
}

.image-carousel-swiper .swiper-slide-active {
  transform: scale(1);
  opacity: 1;
}

.image-carousel-swiper .swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

/* Placeholder styles */
.carousel-placeholder {
  width: 90%;
  max-width: 1200px;
  margin: 40px auto;
  padding: 60px 40px;
  border: 2px dashed rgba(255, 255, 255, 0.5);
  border-radius: 15px;
  text-align: center;
  color: white;
  position: relative;
  z-index: 1;
}

.carousel-placeholder p {
  margin: 5px 0;
  font-size: 1.2rem;
  font-family: 'Georgia', serif;
}

.carousel-placeholder code {
  background-color: rgba(0, 0, 0, 0.3);
  padding: 3px 6px;
  border-radius: 4px;
  font-family: 'Courier New', Courier, monospace;
}

/* Swiper Navigation and Pagination */
.image-carousel-swiper .swiper-button-next,
.image-carousel-swiper .swiper-button-prev {
  color: #ffffff;
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 50%;
  width: 44px;
  height: 44px;
  transition: background-color 0.3s;
}

.image-carousel-swiper .swiper-button-next:hover,
.image-carousel-swiper .swiper-button-prev:hover {
  background-color: rgba(0, 0, 0, 0.5);
}

.image-carousel-swiper .swiper-pagination-bullet {
  background: rgba(255, 255, 255, 0.7);
  width: 10px;
  height: 10px;
}

.image-carousel-swiper .swiper-pagination-bullet-active {
  background: #ffffff;
}
